import { Component, OnInit, ViewChild, } from '@angular/core';
import { JsonCreate } from 'src/app/services/jsonCreate.service';
import * as moment from 'moment'
import { ChartOptions, ChartType, ChartDataSets } from 'chart.js';
import * as pluginDataLabels from 'chartjs-plugin-datalabels';
import { Label,  } from 'ng2-charts';
import { Subject } from 'rxjs';






@Component({
  selector: 'app-profile-store',
  templateUrl: './profile-store.component.html',
  styleUrls: ['./profile-store.component.sass']
})
export class ProfileStoreComponent implements OnInit {
  jsonData
  jsonTrackerData =[]
  jsonTrackerDataOptions=[]
  haveTasks = false
  haveTracker = false
  checked = false
  quantity =0
  dateToday= moment().format('DD-MM-YYYY')
  tasks = {
    date: this.dateToday,
    tasksich: []
  }
  statisticTrue=[
    {name: 'Пробежка', options: [4,13]},
    {name: 'Прыжки', options: [31,14]},
    {name: 'Скачки', options: [22,12]},

]
 

  public barChartOptions: ChartOptions = {
    responsive: false,
    // We use these empty structures as placeholders for dynamic theming.
    scales: { xAxes: [{}], yAxes: [{}] },
    plugins: {
      datalabels: {
        anchor: 'end',
        align: 'end',
      }
    }
  };
  public barChartLabels: Label[] = ['Декабрь','Январь','Февраль'];
  public barChartType: ChartType = 'bar';
  public barChartLegend = true;
  public barChartPlugins = [pluginDataLabels];

  public barChartData: ChartDataSets[] = [
    { data: this.statisticTrue[0].options, label: this.statisticTrue[0].name },
    { data: this.statisticTrue[1].options, label: this.statisticTrue[1].name }
  ];
    
  constructor(private js: JsonCreate,) {
    

    

    }

    
    

  
    
   

   width 
   widthsmall=false
   id
   shoes2


  
  @ViewChild("value",{static: false}) value
  step: number
  @ViewChild("spinner",{static: false}) spinner
  @ViewChild("listTracker",{static: false}) listTracker
  @ViewChild("optionsTracker",{static: false}) optionsTracker
  @ViewChild("shoes",{static: false}) 
  shoes: {
    options,
    selectedOptions,
  }


  click = 0
  private ngUnsubscribe: Subject<void> = new Subject<void>();
  
  ngOnInit() {
    console.log('ЭТО Я NGONINIT')
    this.width = window.innerWidth;
    if(this.width<720){
      this.widthsmall= true
    }

    const subscription = this.js.getSelectedDataFirestore('check-list','date',moment().format('DD-MM-YYYY')).subscribe(
      value => {
      this.jsonData = value[0].tasks
      this.id= value[0].id
      if(this.jsonData===undefined) {
        this.haveTasks = false
      }else this.haveTasks = true
      console.log(this.jsonData)
      console.log(this.haveTasks)
      subscription.unsubscribe()
    })

    const subscriptionTracker =  this.js.getCollectionFirestore('tracker').subscribe(value => {
      value.forEach(val=> {
        this.jsonTrackerData.push({name:val.id,status:false})
      })
      if(this.jsonTrackerData.length===0) {
        this.haveTracker = false
      }else this.haveTracker = true

      console.log(this.haveTracker,"sd")

      for (let i = 0; i <this.jsonTrackerData.length; i++) {
        this.js.getTrackerStatusFirestore('tracker',this.jsonTrackerData[i].name,this.dateToday).subscribe(value => {
          if(value.status===true){
            this.jsonTrackerData[i].status= true
          }
         });
        
      }
      console.log(this.jsonTrackerData)
      subscriptionTracker.unsubscribe()
    })

    

    // const subscriptionTrackerStatus =  this.js.getTrackerStatusFirestore('tracker',this.jsonData,this.dateToday).subscribe(value => {
    //  console.log(value)
    // });


    //   subscriptionTrackerStatus.unsubscribe()


      this.quantity=0

      for (let i = 0; i<this.jsonData.length;i++) {
        if(this.jsonData[i].status=true){
          this.quantity++
        }
      }


      this.click=Math.floor((100/this.jsonData.length)*this.jsonData.length)


      if(this.click===100){
        this.value.nativeElement.style.left ='41px'
      }
      else if(this.click===0) {
        this.value.nativeElement.style.left ='55px'
      }
      else this.value.nativeElement.style.left ='48px'
    
    




















  }
 

  
  


  changeStatus(i,options) {

    
    this.jsonData[i].status = !this.jsonData[i].status
    console.log(this.jsonData)
    
  }

  changeStatusTracker(i,options) {

    
    this.jsonTrackerData[i].status = !this.jsonTrackerData[i].status
    console.log(this.jsonTrackerData)
    
  }

  checkStatus(){
    console.log(this.jsonData)
  }


  updateStatusData() {

    console.log('okey')
    if(this.haveTasks){
    this.js.updateStatus(this.id,"check-list",
    {date:this.dateToday,
     tasks:this.jsonData
    })
  }else console.log('Нету заданий')


  if(this.haveTracker){
    for (let i = 0; i <this.jsonTrackerData.length; i++) {
      console.log(this.jsonTrackerData)
      console.log(this.jsonTrackerData[i].status)
      this.js.setTrackerStatusFirestore('tracker',this.jsonTrackerData[i].name,this.dateToday,this.jsonTrackerData[i].status)
      
    }
  }else console.log('нету трекеров')
    
  }


 

  clickBtn() {
    console.log(this.id)
    console.log(this.shoes)
    this.step= 100/this.shoes.options.length
    this.click = Math.floor(this.step*this.shoes.selectedOptions.selected.length)
    
    if(this.click===100){
      this.value.nativeElement.style.left ='41px'
    }
    else if(this.click===0) {
      this.value.nativeElement.style.left ='55px'
    }
    else this.value.nativeElement.style.left ='48px'


  }

 
}
