import { Component, OnInit, HostListener } from '@angular/core';

@Component({
  selector: 'app-organaizer',
  templateUrl: './organaizer.component.html',
  styleUrls: ['./organaizer.component.sass']
})
export class OrganaizerComponent implements OnInit {

  organaizerData = {
    vertickal: [
     "Дни недели","monday","wensday","thirsday","friday","sunday"
    ],
    horizontal: {
      gaim: [
        {time:'10:30',act:'Прыгать на батуте'},
      ]
    },
      
  }
    
  Data =[
    []

  ]


 
  

  constructor() { }

    width
    breakpoint

  ngOnInit() {
    this.width= window.innerWidth
    if(this.width<576){
      this.breakpoint= "mobile"
    }
    else this.breakpoint = "pc"
  }

  @HostListener('window:resize', ['$event'])
  onResize(event: any) {
      this.width = event.target.innerWidth;
      if(this.width<576){
        this.breakpoint= "mobile"
      }
      else this.breakpoint = "pc"
  }
 

}
