import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ProfileStoreComponent } from './content-item/profile-store/profile-store.component';
import { DnevnikStoreComponent } from './content-item/dnevnik-store/dnevnik-store.component';
import { DnevnikReadComponent } from './content-item/dnevnik-store/dnevnik-read/dnevnik-read/dnevnik-read.component';
import { DnevnikEditorComponent } from './content-item/dnevnik-store/dnevnik-editor/dnevnik-editor.component';
import { OrganaizerComponent } from './content-item/organaizer/organaizer.component';
import { TestComponent } from './test/test.component';
import { CheckListComponent } from './content-item/check-list/check-list.component';
import { ShotComponent } from './content-item/shot/shot.component';
import { RegisterComponent } from './register/register.component';
import { LoginComponent } from './login/login.component';
import { GuardsService } from './services/guards.service';
import { map} from 'rxjs/operators';
import{ canActivate} from '@angular/fire/auth-guard';

import { AngularFireAuthGuard, hasCustomClaim, redirectUnauthorizedTo, redirectLoggedInTo } from '@angular/fire/auth-guard';
import { TrackerComponent } from './content-item/tracker/tracker.component';


const redirectUnauthorizedToLogin = () => redirectUnauthorizedTo(['login']);
const redirectLoggedInToProfile = () => redirectLoggedInTo(['profile']);

const routes: Routes = [
  
  {path: 'register', component: RegisterComponent,...canActivate(redirectLoggedInTo(['profile']))},
  {path: 'login', component: LoginComponent,...canActivate(redirectLoggedInTo(['profile']))},

  {path:'dnevnik-store', component: DnevnikStoreComponent},
  
  {path: 'dnevnik-read', component: DnevnikReadComponent,
  canActivate: [AngularFireAuthGuard],
},


  {path: 'dnevnik-editor', component: DnevnikEditorComponent,
  canActivate: [AngularFireAuthGuard]},

  {path: 'organaizer', component: OrganaizerComponent,
  canActivate: [AngularFireAuthGuard]},

  {path: 'test', component:  TestComponent,
  canActivate: [AngularFireAuthGuard]},

  {path: 'tasks-check',component:CheckListComponent,
  canActivate:[GuardsService]},

  {path: 'tracker', component: TrackerComponent,
  canActivate: [AngularFireAuthGuard]},

  {path: 'shoot-training', component: ShotComponent,
  canActivate: [AngularFireAuthGuard]},
  
  {path: 'profile', component: ProfileStoreComponent,
  canActivate: [AngularFireAuthGuard]},


  {path:'' , redirectTo: '/login', pathMatch: 'full'},
  { path: '**', redirectTo: 'profile', pathMatch: 'full'},

  
];

@NgModule({
  imports: [RouterModule.forRoot(routes,)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
